header {
  color: #6b828f;
  background: #1e2931;
  width: 400px;
  position: relative;
  text-align: center;
  min-height: 100vh;
}

header::before {
  content: '';
  display: block;
  position: absolute;
  top: 0px;
  right: 0;
  width: 1px;
  height: 130px;
  background-color: #2b3c46;
}

.logo {
  padding: 50px;
  padding-bottom: 10px;
  font-weight: 300;
  font-size: 36px;
}

.sublogo {
  font-size: 14px;
}

.iconLink {
  color: #6b828f;
  text-decoration: none;
  font-size: 13px;
  font-family: "Open Sans", serif;
  position: relative;
}

.iconLink::before {
  content: '';
  display: block;
  position: absolute;
  top: -12px;
  right: -93px;
  width: 500px;
  height: 1px;
  background-color: #2b3c46;
}

.ul {
  list-style: none;
  margin-top: 70px;
  margin-bottom: 70px;
}

.navlink {
  text-decoration: none;
  color: #6b828f;
  display: block;
  height: 45px;
  font-size: 18px;
  line-height: 45px;
}

.navlinkActive {
  color: rgb(208, 224, 231);
  background: #2b3c46;
  text-decoration: none;
  position: relative;
}

.navlinkActive::before {
  content: '';
  background: #1e8a71;
  height: 45px;
  width: 4px;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
}
